.home-page {
  .home-info {
    padding-top: 40px;
    padding-bottom: 40px;
    .info-content {
      margin-left: 30px;
      .info-name {
        padding-top: 20px;
      }
    }
    .main-logo{
      max-height: 97px;
    }
  }
  .page-element{
    display: flex;
    justify-content: center;
    .img-smile {
      margin-left: 15px;
      height: 50px;
      width: 50px;
    }
    .img-ellipe {
      height: 15px;
      width: 15px;
    }
  }
  .home-info2 {
    padding-top: 30px;
    padding-bottom: 100px;
    font-size: $medium_text;
    .info-lage {
      padding-top: 20px;
      font-size: $heading_4;
    }
  }
  .home-info3 {
    background: $primary_color;
    padding-top:80px;
    .text-large {
      padding: 10px 0;
      font-family: 'AmericanaBT', serif;
      font-size: 94px;
      line-height: 115px;
    }
    .brands-left{
      padding-right:15px !important;
    }
    .brands {
      padding-left: 15px !important;
      display: flex;
      width: 100%;
      height: 46px;
      .brands-line {
        margin-left: 10px;
        width: 100%;
        border-bottom: 1px solid #000000;
      }
    }
    .child1{
      padding-right:15px !important;
    }
    .child2{
      padding-left: 15px !important;
      padding-right: 15px !important;
    }
    .child3{
      padding-left: 15px !important;
      display: flex;
      justify-content: end;
    }
    .img-box{
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        display: block;
        margin: auto;
      }
    }
    .small-img{
      position: relative;
      img{
        float:right;
      }
    }
  }
}
.img-des {
  overflow: hidden;
  img {
    width: 100%;
    height: auto;
    filter: grayscale(100%);
    transition: all 1s ease;
  }
  img:hover {
    filter: grayscale(0%);
  }
}
.pj_name{
  display: flex;
  align-items: center;
}
.pj_intro{
  width: 80%;
}
.my-des-title{
  padding-top:100px;
  padding-bottom:60px;
}
.des_r1{
  padding-bottom: 80px;
}
.sentence-container {
  width: 100%;
  height: 430px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1200px) {
  body {
    background-color: lightblue;
    .home-page{
      .home-info3{
        .text-large{
          font-size:60px;
        }
      }
    }
  }
}

@media only screen and (max-width: 992px) {
  body{
    .active{
      justify-content: left;
      padding-left: 20px;
    }
    .container-fluid{
      padding-left: 35px !important;
      padding-right:35px !important;
    }
    .body_text {
      font-size: 12px;
      line-height: 18px;
    }
    .back-to-top {
      right: -14px;
      bottom: 20%;
    }
    .get-in-touch {
      left: -14px;
      bottom: 20%;
    }
  }
}

@media only screen and (max-width: 768px) {
  body {
    .active{
      justify-content: left;
      padding-left: 20px;
    }
    .container-fluid{
      padding-left: 35px !important;
      padding-right:35px !important;
    }
    .body_text {
      font-size: 12px;
      line-height: 18px;
    }
    .back-to-top {
      right: -14px;
      bottom: 20%;
    }
    .get-in-touch {
      left: -14px;
      bottom: 20%;
    }
    .home-page{
      .home-info3{
        .text-large{
          font-size:54px;
        }
      }
      .home-info2{

      }
    }
  }
}

@media only screen and (max-width: 576px) {
  body {
    background-color: lightblue;

    .home-page{
      .main-logo{
        max-height: 47px;
      }
    }
  }
}

